@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;300;400;500;600;700&display=swap);
:root {
  --accent: #A8DEFF;
  --background: #111622;
  --title-font: 'Josefin Sans', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: 'Josefin Sans', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #111622;
  background: var(--background);
  background: black;
  color: white;
}
.home {
  min-height: 100vh;
}

.home nav {
  height: auto;
  display: flex;
  align-items: center;
  padding: 40px;
  padding-bottom: 20px;
}

.home nav .name {
  color: var(--accent);
  font-size: 2rem;
  font-family: var(--title-font);
  line-height: 2.5rem;
  position: relative;
}

.home .works {
  padding: 0 40px;
  color: var(--accent);
  position: relative;
  transition: height 600ms ease;
}

.home .works .shade {
  position: absolute;
  bottom: 0;
  left: 0;
  color: var(--accent);
  width: 100%;
  height: 100px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  background: linear-gradient(to top, var(--background), transparent);
}

.home .works .shade span {
  padding: 10px;
  cursor: pointer;
}

.home .jobless {
  color: rgba(255, 255, 255, 0.274);
  padding: 0 5px;
}

.home .work-videos {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(320px, auto))[auto-fit];
      grid-template-columns: repeat(auto-fit, minmax(320px, auto));
  grid-gap: 30px;
  padding: 20px 0;
}

@keyframes slidein {
  0% {
    transform: translateY(10px) rotate(5deg);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}

.home .work {
  background: var(--background);
  border-radius: 6px;
  box-shadow: 0 6px 30px 1px rgba(0, 155, 245, 0.15);
  overflow: hidden;
}

.home .work .cover-img {
  width: auto;
  height: 200px;
}

.home .work .cover-img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.home .work .about {
  padding: 15px 20px;
}

.home .work .title {
  font-size: 1.6rem;
  display: block;
  color: white;
  cursor: pointer;
}

.home .work .description {
  color: rgba(255, 255, 255, 0.623);
  display: block;
}

.home .work .dev-status {
  display: flex;
  align-items: center;
}

.home .work .dev-status .indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.home .work .dev-status span {
  margin-left: 8px;
}

.home .work .more-info {
  display: flex;
}

.home .work .link-logo {
  margin: 5px;
  width: 30px;
  height: 30px;
  object-fit: contain;
  cursor: pointer;
}

.home > .about {
  display: flex;
  text-align: center;
}

.home > .about .in {
  padding: 40px;
}

.home > .about .in .name {
  font-size: 2rem;
}

.home > .about .memberof {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.home > .about .memberof .club {
  margin: 20px;
  width: 200px;
  object-fit: contain;
}

@media only screen and (max-width: 850px) {
  .home > .about {
    flex-direction: column;
  }
  .home > .about .profile-img {
    width: 100%;
  }
  .home > .about .profile-img img {
    width: 100%;
    height: 100%;
  }
}

.home > .about .profile-img {
  width: 400px;
  height: 400px;
}

.home > .about .profile-img img {
  width: 400px;
  height: 100%;
  object-fit: scale-down;
}

.home .scales {
  margin-bottom: 8px;
}

.home .scales .scale {
  display: flex;
  align-items: center;
}

.home .scales .dot {
  width: 10px;
  height: 10px;
  margin-right: 10px;
  background: #81ff81;
  border-radius: 50%;
}

.home .profile-data {
  padding: 40px;
}

.home .profile-data .name {
  font-size: 2rem;
}

.home .more-data {
  display: flex;
  flex-wrap: wrap;
  margin: -12px 0 0 -12px;
  width: calc(100% + 12px);
}

.home .more-data > * {
  margin: 12px 0 0 12px;
}

.home footer {
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.socials {
  display: flex;
  align-items: center;
}
